<template>
  <div class="detail">
    <div>
      <van-nav-bar
        class="bar"
        title="书本详情"
        left-text="返回"
        left-arrow
        @click-left="onClickLeft"
        @click-right="onClickRight"
      >
        <template #right>
          <van-icon :name="show ? 'cross' : 'ellipsis'" size="18" />
        </template>
      </van-nav-bar>
      <van-popup v-model="show" position="top" :style="{ height: '171.2px'}">
        <van-grid :column-num="3">
          <van-grid-item
            v-for="(item,index) in popup"
            :key="index"
            :icon="item.icon"
            :text="item.text"
            @click="popupClick(item)"
          />
        </van-grid>
      </van-popup>
    </div>
    <div class="bookDetail-item clearfix">
      <div class="bookDetail-img fl">
        <img class="auto-img" :src="bookDetail.cover" alt />
      </div>
      <div class="bookDetail-detail fl">
        <div class="bookDetail-title">{{bookDetail.title}}</div>
        <div class="bookDetail-author">作者：{{bookDetail.author}}</div>
        <div class="bookDetail-author">
          <van-rate
            v-model="value"
            allow-half
            void-icon="star"
            color="#ffd21e"
            void-color="#eee"
            readonly
          />
          {{rateData.score}}分
          /{{rateData.count}}人评过
        </div>
        <div class="bookDetail-author">字数：{{bookDetail.wordCount}}万字</div>
        <div class="bookDetail-author">分类：{{bookDetail.majorCate}}/{{bookDetail.minorCate}}</div>
      </div>
    </div>
    <div class="btn clearfix">
      <van-button
        class="fl"
        :disabled="isRead"
        :color="isRead ? '#ed424b' : '#ccc'"
        @click="addBook"
      >{{isRead ? '已在书架' : '加入书架'}}</van-button>
      <van-button class="fr" color="#ed424b" @click="read">开始阅读</van-button>
    </div>
    <div class="bookDetail-lastChapter one-text" @click="read">
      <van-tag type="danger" plain color="#fff" text-color="#ed424b">最新</van-tag>
      {{bookDetail.lastChapter}}
    </div>
    <div class="longIntro clearfix">
      <p class="bookDetail-longIntro" :class="isOver ? 'overText' : ''">{{bookDetail.longIntro}}</p>
      <div class="over">
        <van-icon :name="isOver ? 'arrow-down' : 'arrow-up'" @click="over" size="20" />
      </div>
    </div>
    <div class="tag">
      <van-cell class="cell" title="标签" />
      <div class="tags">
        <van-tag
          plain
          size="large"
          round
          v-for="(item,index) in bookDetail.tags"
          :key="index"
        >{{item}}</van-tag>
      </div>
    </div>
    <div class="catalog one-text" @click="goC('Catalog')">
      <van-cell title="目录" is-link>
        <template #default>
          <span>{{first.title}}</span>
        </template>
      </van-cell>
    </div>
    <div class="interested">
      <van-cell>
        <template #title>
          <div class="title one-text">
            读过
            <span>{{bookDetail.title}}</span>
            的人还喜欢
          </div>
        </template>
        <template #default>
          <div @click="more">更多</div>
        </template>
      </van-cell>
      <div
        class="detail-item clearfix"
        v-for="(item,index) in interestedData"
        :key="index"
        @click="goPage(item)"
      >
        <div class="detail-img fl">
          <img class="auto-img" v-lazy="item.cover" alt />
        </div>
        <div class="detail-detail fl">
          <div class="detail-title one-text">{{item.title}}</div>
          <div class="detail-author one-text">作者：{{item.author}}</div>
          <div class="detail-shortIntro one-text">简介：{{item.shortIntro}}</div>
          <div class="detail-lastChapter one-text">
            <span class="fl">类型：{{item.minorCate}}</span>
            <span class="fr">{{item.retentionRatio}}%的读者看过这本书</span>
          </div>
        </div>
      </div>
    </div>
    <div class="comment">
      <van-cell>
        <template #title>
          <div class="title one-text">
            <span>{{bookDetail.title}}</span>
            评论
          </div>
        </template>
      </van-cell>
      <div v-if="commentsData.length == 0" class="empty">暂时没有评论！</div>
      <div v-else>
        <div class="comment-item clearfix" v-for="(item,index) in commentsData" :key="index">
          <div class="comment-img fl">
            <img class="auto-img" :src="require('../assets/head/'+item.author.lv+'.png')" alt />
          </div>
          <div class="comment-detail fl">
            <div class="comment-title">{{item.author.nickname}}</div>
            <div class="comment-content">{{item.content}}</div>
            <div class="comment-total">
              <div class="comment-icon">
                <van-rate
                  v-model="item.rating"
                  icon="like"
                  color="#ed424b"
                  void-color="#eee"
                  void-icon="like-o"
                  readonly
                  size="14px"
                />
              </div>
              <div class="comment-like clearfix">
                <div class="fl">{{item.created}}</div>
                <div class="fr good">
                  <van-icon name="good-job-o" class="icon" />
                  {{item.likeCount}}
                  人觉得很赞
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { createNamespacedHelpers, mapGetters } from "vuex";
const { mapState, mapMutations } = createNamespacedHelpers("bookModule");
export default {
  data() {
    return {
      value: 0,
      detailId: {},
      bookDetail: [],
      rateData: [],
      bookSourcesId: "",
      bookChaptersData: [],
      isOver: true,
      isRead: false,
      show: false,
      popup: [
        {
          icon: "home-o",
          text: "主页",
          route: "Home"
        },
        {
          icon: "contact",
          text: "账户",
          route: "Login"
        },
        {
          icon: "search",
          text: "搜索",
          route: "Search"
        },
        {
          icon: "bookmark-o",
          text: "书架",
          route: "Bookshelf"
        },
        {
          icon: "bar-chart-o",
          text: "排行榜",
          route: "Ranking"
        }
      ],
      interestedData: [],
      commentsData: [],
      first: []
    };
  },
  created() {
    this.detailId = this.$route.query;
    this.getBookDetail();
    this.getbookSources();
    if (this.addBookId.length > 0) {
      for (let i = 0; i < this.addBookId.length; i++) {
        if (this.detailId.id == this.addBookId[i]._id) {
          this.isRead = true;
        }
      }
    }
    this.getInterestedData();
    this.comment();
  },
  computed: {
    ...mapState(["addBookId"])
  },
  methods: {
    ...mapMutations(["changeData"]),
    goC(route) {
      let ids = this.bookSourcesId;
      this.$router.push({ name: route, query: { id: ids } });
    },
    //加入书架
    addBook() {
      if (localStorage.getItem("bookTK")) {
        this.isRead = !this.isRead;
        if (this.isRead) {
          this.bookDetail.isRead = true;
          this.$toast("加入书架成功！");
          this.changeData({
            //**********addBookId要和传回来的值一致*****//
            key: "addBookId",
            value: this.bookDetail
          });
        }
      } else {
        this.$dialog.confirm({
          title: "加入书架失败",
          message: "你还没有登录哦！"
        })
          .then(() => {
            // on confirm
            this.$router.push({ name: 'Login' });
          })
          .catch(() => {
            // on cancel
          });
      }
    },
    //返回
    onClickLeft() {
      this.$router.go(-1);
    },
    onClickRight() {
      this.show = !this.show;
    },
    popupClick(item) {
      this.$router.push({ name: item.route });
    },
    //获取书籍详情
    getBookDetail(id) {
      let ids = "";
      if (id != undefined) {
        ids = id;
      } else {
        ids = this.detailId.id;
      }
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });
      this.axios({
        method: "GET",
        url: "http://novel.kele8.cn/book-info/" + ids
      })
        .then(result => {
          this.$toast.clear();

          if (Object.keys(result.data).length > 0) {
            result.data.cover = this.imgKey(result.data.cover);
            let str = result.data.wordCount.toString();
            let strW = "";
            let strX = "";
            if (str.length >= 5) {
              strW = str.replace(str.substring(str.length - 4), "");
              strX = str.substring(str.length - 4).slice(0, 2);
            } else {
              strW = 0;
              strX = str.substring(str.length - 4).slice(0, 2);
            }
            str = `${strW + "." + strX}`;
            result.data.wordCount = str;
            if (result.data.tags.length >= 5) {
              result.data.tags = result.data.tags.slice(0, 5);
            }
            this.bookDetail = result.data;
            this.rateData = this.bookDetail.rating;
            this.value = this.rateData.score / 2;
          }
        })
        .catch(err => {
          this.$toast.clear();
        });
    },
    //图片解码
    imgKey(value) {
      return unescape(value).slice(7);
    },
    getbookSources() {
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });
      this.axios({
        method: "GET",
        url:
          "http://novel.kele8.cn/book-sources?view=summary&book=" +
          this.detailId.id
      })
        .then(result => {
          this.$toast.clear();

          if (Object.keys(result.data).length > 0) {
            this.bookSourcesId = result.data[0]._id;
            if (this.bookSourcesId != "") {
              this.getbookChapters();
            }
          }
        })
        .catch(err => {
          this.$toast.clear();
        });
    },
    getbookChapters() {
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });
      this.axios({
        method: "GET",
        url: "http://novel.kele8.cn/book-chapters/" + this.bookSourcesId
      })
        .then(result => {
          this.$toast.clear();

          if (Object.keys(result.data).length > 0) {
            this.bookChaptersData = result.data.chapters;
            this.first = result.data.chapters[0];
          }
        })
        .catch(err => {
          this.$toast.clear();
        });
    },
    read() {
      let bookId = this.bookSourcesId;
      this.$router.push({
        name: "Book",
        query: { bookId }
      });
    },
    over() {
      this.isOver = !this.isOver;
    },
    getInterestedData() {
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });
      this.axios({
        method: "GET",
        url: "http://novel.kele8.cn/recommend/" + this.detailId.id
      })
        .then(result => {
          this.$toast.clear();

          if (Object.keys(result.data).length > 0) {
            //对数据加锁图片解码
            for (let i = 0; i < result.data.books.length; i++) {
              result.data.books[i].cover = this.imgKey(
                result.data.books[i].cover
              );
              result.data.books[i].retentionRatio = result.data.books[
                i
              ].retentionRatio
                .toString()
                .split(".")[0];
            }
            if (Object.keys(result.data.books).length > 3) {
              this.interestedData = result.data.books.slice(0, 3);
            } else {
              this.interestedData = result.data.books;
            }
          }
        })
        .catch(err => {
          this.$toast.clear();
        });
    },
    //跳转到小说详情页
    goPage(item) {
      let ids = item._id;
      document.documentElement.scrollTop = 0;
      this.getBookDetail(ids);
    },
    more() {
      let moreId = this.detailId.id;
      let more = "more";
      this.$router.push({
        name: "Classify",
        query: { id: moreId, more: more }
      });
    },
    comment() {
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });
      this.axios({
        method: "GET",
        url: "http://novel.kele8.cn/book/short-reviews?book=" + this.detailId.id
      })
        .then(result => {
          this.$toast.clear();

          if (Object.keys(result.data).length > 0) {
            result.data.docs = result.data.docs.slice(0, 3);
            for (let i = 0; i < 3; i++) {
              result.data.docs[i].created = this.resolvingDate(
                result.data.docs[i].created
              );
            }
            this.commentsData = result.data.docs;
          }
        })
        .catch(err => {
          this.$toast.clear();
        });
    },
    resolvingDate(date) {
      //date是传入的时间
      let d = new Date(date);
      let month =
        d.getMonth() + 1 < 10 ? "0" + (d.getMonth() + 1) : d.getMonth() + 1;
      let day = d.getDate() < 10 ? "0" + d.getDate() : d.getDate();
      let hours = d.getHours() < 10 ? "0" + d.getHours() : d.getHours();
      let min = d.getMinutes() < 10 ? "0" + d.getMinutes() : d.getMinutes();
      let sec = d.getSeconds() < 10 ? "0" + d.getSeconds() : d.getSeconds();
      let times =
        d.getFullYear() +
        "-" +
        month +
        "-" +
        day +
        " " +
        hours +
        ":" +
        min +
        ":" +
        sec;
      return times;
    }
  }
};
</script>

